footer {
  background-color: rgb(13, 12, 14);
  width: 100%;
  padding: 1px 0;
  margin-top: 10px;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  letter-spacing: .03rem;
}

.footer__text {
  text-align: center;
  font-size: 1.2rem;
  color: #dddddd;
  /* margin: 0;  */
}

.footer__link {
  color: #ffffff;  
  font-weight: bold;
  text-decoration: none;
}

.social {
  width: 20px;
  height: 20px;
  margin: 0 10px 0;
}



